import "bootstrap/dist/css/bootstrap.css";
import "bootstrap/dist/js/bootstrap.js";
import $ from "jquery";
import "./style.scss";
import anime from "animejs";

// Hover Effect

$(
	".animationTrigger:nth-child(1), .subElementCategory:nth-child(1)"
).mouseenter(() => {
	anime({
		targets: ".elementStatic:nth-child(1)",
		translateX: "-50%",
		translateY: "-50%",
		scale: 1.2,
	});
	anime({
		targets: ".subElementStatic:nth-child(1)",
		opacity: 0,
	});
	anime({
		targets: ".subElementCategory:nth-child(1)",
		opacity: 1,
	});
});
$(
	".animationTrigger:nth-child(1), .subElementCategory:nth-child(1)"
).mouseleave(() => {
	anime({
		targets: ".elementStatic:nth-child(1)",
		translateX: "-50%",
		translateY: "-50%",
		scale: 1,
	});
	anime({
		targets: ".subElementStatic:nth-child(1)",
		opacity: 1,
	});
	anime({
		targets: ".subElementCategory:nth-child(1)",
		opacity: 0,
	});
});
