// Removing Startup Animation

setTimeout(() => {
	$(".element").addClass("elementStatic");
	$(".element").removeClass("element");
	$(".elementStatic").css({
		transform: "translateX(-50%) translateY(-50%) scale(1)",
	});

	$(".subElement").addClass("subElementStatic");
	$(".subElement").removeClass("subElement");
	$(".subElementStatic").css({ opacity: 1 });

	$(".subElementCategory").css({ opacity: 0 });
	$(".animationTrigger").css({ display: "block" });
}, 2500);

// Static control

if (window.innerWidth <= 699) {
	document.location = "./mobile/index.html";
} else {
	$("html").css({ "font-size": window.innerWidth / 80 + "px" });
	$(
		".subElementCategoryHolder, .subElementCategory, .subElementCategoryIcon"
	).css({ "z-index": 8 });
}
